import React, {useEffect, useState} from "react";
import TitleComponent from "../../../components/Title/TitleComponent";
import {useDispatch, useSelector} from "react-redux";
import {fetchAllMemberByUserId, fetchPostOrderJerseyAction} from "../../../app/service/action/memberAction";
import imageSize from "../../../assets/image/size_jersey.png";
import {checkImage} from "../../../utils/Utils";
import InputText from "../../../components/Input/InputText";
import {InputFileImage} from "../../../components/Input/InputFileImage";
import {successAlert, warningAlert} from "../../../app/service/helper/handlingError";
import Loading from "../../../components/Loading/Loading";
import {DATA_OPTION_JERSEY} from "../../../utils/dummyData";
import SelectBox from "../../../components/Input/SelectBox";

const INITIAL_OBJECT = {
    registrationMemberId: '',
    sizeJersey: '',
    jerseyNumber: '',
    payment: ''
}
export const OrderJersey = () => {
    const dispatch: any = useDispatch();
    const [loading, setLoading] = useState(false)
    const [dataOptionMember, setDataOptionMember] = useState([])
    const [requestParam, setRequestParam] = useState(INITIAL_OBJECT)

    const [errorJerseyNumber, setErrorJerseyNumber] = useState({
        isError: false,
        errorMessage: ''
    })
    const [errorSizeJersey, setErrorSizeJersey] = useState({
        isError: false,
        errorMessage: ''
    })
    const [errorPayment, setErrorPayment] = useState({
        isError: false,
        errorMessage: ''
    })

    const data = useSelector(({membersByUserIdState}) => membersByUserIdState.membersByUserId.data);
    const isLoading = useSelector(({membersByUserIdState}) => membersByUserIdState.membersByUserId.isLoading);
    const status = useSelector(({membersByUserIdState}) => membersByUserIdState.membersByUserId.status);

    const dataOrderJersey = useSelector(({orderJerseyState}) => orderJerseyState.orderJersey.data);
    const isLoadingOrderJersey = useSelector(({orderJerseyState}) => orderJerseyState.orderJersey.isLoading);
    const statusOrderJersey = useSelector(({orderJerseyState}) => orderJerseyState.orderJersey.status);

    const updateFormValue = ({updateType, value}: any) => {
        setRequestParam({...requestParam, [updateType]: value});
    }

    const updateImageValue = ({updateType, value}: any) => {
        // setErrorMessage("")
        setRequestParam({...requestParam, [updateType]: value});
    }

    useEffect(() => {
        dispatch(fetchAllMemberByUserId())
    }, [dispatch]);

    useEffect(() => {
        if (status === "ready" && data.result) {
            const update = data.result.map((item: any) => ({
                value: item.id,
                name: item.fullname
            }))
            setDataOptionMember(update)
        }
    }, [data.result, status]);

    const handleSubmit = () => {
        if (checkRequest()) {
            setLoading(true)
            // @ts-ignore
            dispatch(fetchPostOrderJerseyAction(requestParam))
        }
    }

    const showAlert = (status: boolean) => {
        if (status) {
            setRequestParam(INITIAL_OBJECT)
            return successAlert('Pesan jersey berhasil dikirim!!!')
        } else {
            return warningAlert(dataOrderJersey.responseData.message)
        }
    }

    useEffect(() => {
        if (statusOrderJersey === 'ready') {
            if (dataOrderJersey.responseData.code === 200) {
                showAlert(true)
            } else {
                showAlert(false)
            }
        }
    }, [dataOrderJersey.result, statusOrderJersey]);

    function checkRequest() {

        let result = true;
        if (requestParam.sizeJersey === "") {
            setErrorSizeJersey({
                isError: true,
                errorMessage: 'SizeJersey wajib diisi!!!'
            })
            result = false
        } else {
            setErrorSizeJersey({
                isError: false,
                errorMessage: ''
            })
        }

        if (requestParam.jerseyNumber === "") {
            setErrorJerseyNumber({
                isError: true,
                errorMessage: 'Nomor Jersey wajib diisi!!!'
            })
            result = false
        } else {
            setErrorJerseyNumber({
                isError: false,
                errorMessage: ''
            })
        }

        if (requestParam.payment === "") {
            setErrorPayment({
                isError: true,
                errorMessage: 'Bukti transfer wajib diisi!!!'
            })
            result = false
        } else {
            setErrorPayment({
                isError: false,
                errorMessage: ''
            })
        }

        return result
    }

    return (
        <div className={'order-jersey'}>
            <TitleComponent title={'Order Jersey'}/>
            {isLoading ? <Loading/> :
                // {isLoadingOrderJersey ? <Loading/> :
                <>
                    <div>
                        <SelectBox labelTitle={''}
                                   placeholder={"--Silahkan Pilih Rider--"}
                                   defaultValue={requestParam.registrationMemberId}
                                   updateFormValue={updateFormValue}
                                   updateType={'registrationMemberId'}
                                   options={dataOptionMember}/>
                    </div>
                    {requestParam.registrationMemberId && (
                        <>
                            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                                <div className={'image-size'}>
                                    <img src={imageSize} alt=""/>
                                </div>
                                <SelectBox labelTitle={'Size'} defaultValue={requestParam.sizeJersey}
                                           placeholder={'--Pilih Size--'}
                                           updateFormValue={updateFormValue} updateType={'sizeJersey'}
                                           variant='outlined'
                                           options={DATA_OPTION_JERSEY}
                                           isError={errorSizeJersey.isError}
                                           errorMessage={errorSizeJersey.errorMessage}/>
                                <div>
                                    <InputText labelTitle={'No. Start/Jersey'} updateType={'jerseyNumber'}
                                               variant='outlined'
                                               type={'numeric'}
                                               defaultValue={requestParam.jerseyNumber}
                                               updateFormValue={updateFormValue}
                                               isError={errorJerseyNumber.isError}
                                               errorMessage={errorJerseyNumber.errorMessage}/>
                                    <small className={'text-italic'}>Minimal 3 digit (Contoh: 009)</small>
                                </div>
                            </div>
                            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                                <div className={'image-size'}>
                                    <img src={checkImage(requestParam.payment)} alt=""/>
                                </div>
                                <InputFileImage
                                    updateType={'payment'}
                                    accept={'image/*'}
                                    labelTitle={'Upload Bukti Transfer'}
                                    type={'file'}
                                    variant='outlined'
                                    isError={errorPayment.isError}
                                    errorMessage={errorPayment.errorMessage}
                                    updateFormValue={updateImageValue}/>
                            </div>
                            <div className="button">
                                <button className={'order-jersey-button'}
                                        onClick={handleSubmit}>
                                    Pesan Jersey
                                </button>
                            </div>
                        </>
                    )}
                </>
            }
        </div>
    )
}
